import request from '@/apis/request.wms'
import download from '@/apis/request.download'

// 主列表
export function getListApi(data = {}) {
  return request({
    url: '/warehouseMgt/wmsInventoryBook/pageList',
    method: 'post',
    data,
    timeout: 2 * 60 * 1000
  })
}

